.Prompt {
    background-color: #fff;
    border: 1px solid #e1e1e6;
    border-radius: 8px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    margin-bottom: 2rem;
    padding: 1.5rem;
}

.Prompt h2 {
    font-size: 1.5rem;
    font-weight: 600;
    margin-top: 0;
    margin-bottom: 0.5rem;
}

.Prompt p {
    margin-top: 0;
    margin-bottom: 0;
}
